import React, { useEffect, useState } from 'react';
import emailjs from 'emailjs-com';
import gsap from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";
import "../Styles/Contact.css";

gsap.registerPlugin(ScrollTrigger);

function Contact() {

    const [disable, setDisable] = useState(false);

    useEffect(() => {
        gsap.to('.contact-form', 0, { x: 50 });
        gsap.to('.contact-form', .7, {
            scrollTrigger: {
                trigger: '.contact',
                start: "top 60%",
                end: 'bottom 25%'
            },
            x: 0,
        });
    }, []);

    function sendEmail(e) {
        setDisable(true)
        e.preventDefault();

        let tl = gsap.timeline();
        tl.to('.btn-text', 1, { text: "Sending..." })

        emailjs.sendForm('service_apzyxis', 'template_w4m759g', e.target, 'user_Pgmtn1fOzzBMCnRBXmIxM')
            .then((result) => {
                // success timeline
                document.getElementById("contact-form").reset();
                let successTl = gsap.timeline();
                successTl.to('.btn-text', 1, { y: 100 })
                    .fromTo('.success-circle', .3, { scale: 0 }, { scale: 1, opacity: 1 })
                    .fromTo('.success-symbol', .3, { y: -20, opacity: 0 }, { y: 0, opacity: 1, ease: "back.out(1.7)" })
                    .to('.success-circle', .2, { opacity: 0, scale: 0, delay: 2 })
                    .to('.btn-text', 0, { text: "Send" })
                    .to('.btn-text', 1, { y: 0 });
                setDisable(false);
            }, (error) => {
                // error timeline
                document.getElementById("contact-form").reset();
                let failureTl = gsap.timeline();
                failureTl.to('.btn-text', 1, { y: 100 })
                    .fromTo('.failure-circle', .3, { scale: 0 }, { scale: 1, opacity: 1 })
                    .fromTo('.failure-symbol', .3, { y: -20, opacity: 0 }, { y: 0, opacity: 1, ease: "back.out(1.7)" })
                    .to('.failure-circle', .2, { opacity: 0, scale: 0, delay: 2 })
                    .to('.btn-text', 0, { text: "Send" })
                    .to('.btn-text', 1, { y: 0 });
                setDisable(false);
            });
    }

    return (
        <div id="contact" className="contact d-flex justify-content-center align-items-center">
            <div className="container p-20">


                <div className="contact-form d-flex flex-column justify-content-center align-items-center">

                    <form id="contact-form" onSubmit={sendEmail}>
                        <div className="form-block">
                            <h1 className="contact-heading">Get in touch</h1>
                        </div>
                        <div className="form-block d-flex">
                            <div className="form-group d-flex flex-column">
                                <input type="text" name="from_name" placeholder="Name" required />
                            </div>
                            <div className="form-group d-flex flex-column">
                                <input type="email" name="from_email" placeholder="Email" required />
                            </div>
                        </div>
                        <div className="form-group">
                            <textarea name="message" cols="30" rows="2" placeholder="Message" required ></textarea>
                        </div>

                        <div className="form-group d-flex" style={{ marginTop: "10px" }}>
                            <button className="btn d-flex justify-content-center align-items-center" disabled={disable}>
                                <div className="overflow-hidden position-relative w-100 h-100">
                                    <div className="failure-circle d-flex justify-content-center align-items-center">
                                        <svg xmlns="http://www.w3.org/2000/svg" width="15" height="15" fill="var(--red)" className="failure-symbol bi bi-x" viewBox="0 0 16 16">
                                            <path d="M4.646 4.646a.5.5 0 0 1 .708 0L8 7.293l2.646-2.647a.5.5 0 0 1 .708.708L8.707 8l2.647 2.646a.5.5 0 0 1-.708.708L8 8.707l-2.646 2.647a.5.5 0 0 1-.708-.708L7.293 8 4.646 5.354a.5.5 0 0 1 0-.708z" />
                                        </svg>
                                    </div>
                                    <div className="success-circle d-flex justify-content-center align-items-center">
                                        <svg xmlns="http://www.w3.org/2000/svg" width="15" height="15" fill="var(--green)" className="success-symbol bi bi-check2" viewBox="0 0 16 16">
                                            <path d="M13.854 3.646a.5.5 0 0 1 0 .708l-7 7a.5.5 0 0 1-.708 0l-3.5-3.5a.5.5 0 1 1 .708-.708L6.5 10.293l6.646-6.647a.5.5 0 0 1 .708 0z" />
                                        </svg>
                                    </div>
                                    <div className="btn-text">Send</div>
                                </div>
                            </button>
                        </div>

                    </form>


                </div>


            </div>
        </div>
    );
}

export default Contact;