import React, { useEffect } from "react";
import gsap from "gsap";

export const Loader = () => {
    useEffect(() => {

    }, []);
    return <div className="loader d-flex justify-content-center align-items-center">
        <div className="overflow-hidden"><h1 className="loader-letter">m</h1></div>
        <div className="overflow-hidden"><h1 className="loader-letter">o</h1></div>
        <div className="overflow-hidden"><h1 className="loader-letter" style={{color:"var(--red)"}}>.</h1></div> 
    </div>;
}

export const Orientation = () => {
    useEffect(() => {
        Rotate('.bi-phone');
    }, []);
    return <div className="orientation d-flex flex-column justify-content-center align-items-center">
        <svg xmlns="http://www.w3.org/2000/svg" width="50" height="50" fill="currentColor" className="bi bi-phone" viewBox="0 0 16 16">
            <path d="M11 1a1 1 0 0 1 1 1v12a1 1 0 0 1-1 1H5a1 1 0 0 1-1-1V2a1 1 0 0 1 1-1h6zM5 0a2 2 0 0 0-2 2v12a2 2 0 0 0 2 2h6a2 2 0 0 0 2-2V2a2 2 0 0 0-2-2H5z" />
            <path d="M8 14a1 1 0 1 0 0-2 1 1 0 0 0 0 2z" />
        </svg>
        <p>Please rotate your device back</p>
    </div>;
}

export const Rotate = (element) => {
    let tl = gsap.timeline({ repeat: -1, ease: 'Power2.easeInOut' });
    tl.to(element, 1, {
        rotation: 90,
    })
    tl.to(element, 1, {
        rotation: 0
    })
    tl.to(element, 2, {
        rotation: 0
    })
    tl.to(element, 1, {
        rotation: 90,
    })
    tl.to(element, 1, {
        rotation: 0
    })
    tl.to(element, 2, {
        rotation: 0
    })
}