import React, { useEffect } from 'react';
import gsap from "gsap";
import { TextPlugin } from "gsap/TextPlugin";
import { ScrollTrigger } from "gsap/ScrollTrigger";
import "../Styles/Work.css";


import car from "../Assets/car.JPG";
import game from "../Assets/game.JPG";
import kora from "../Assets/kora.jpg";
import hrms from "../Assets/hrms.jpg";
import expenses from "../Assets/expenses.jpg";
import task from "../Assets/task.jpg";


gsap.registerPlugin(ScrollTrigger);
gsap.registerPlugin(TextPlugin);

function Work() {

    const projects = [
        {
            title: 'Expenses Tracker',
            tags: ['Node.js', 'Express.js', 'React', 'MongoDB'],
            details: 'Track your finances to know where your money is going.',
            github: 'https://github.com/moElhaj/expenses-tracker',
            demo: 'https://mo-expenses.herokuapp.com',
            image: expenses
        },
        {
            title: 'Guess the word',
            tags: ['Angular'],
            details: 'Guess the word using the given clues. A fun game built using Angular.',
            github: 'https://github.com/moElhaj/angular-word-game',
            demo: 'https://mo-game.herokuapp.com',
            image: game
        },
        {
            title: 'Predicting cars prices',
            tags: ['Flask', 'Python'],
            details: 'Estimate cars prices based on features specified by you using Machine Learning.',
            github: 'https://github.com/moElhaj/Machine-Learning-Car-Estimator',
            demo: 'https://codex-garage.herokuapp.com',
            image: car
        },
        {
            title: 'Football App',
            tags: ['React Native'],
            details: 'Follow premier league, live scores, and standings',
            github: 'https://github.com/moElhaj/react-native-football-app',
            demo: 'https://expo.io/@moelhaj/kora',
            image: kora
        },
        {
            title: 'Employee Management',
            tags: ['PHP'],
            details: 'Payrolls and Human Resources Management.',
            github: 'https://github.com/moElhaj/php-employees-management',
            image: hrms
        },
        {
            title: 'Task Management',
            tags: ['ASP.NET'],
            details: 'Manage tasks, sort by priority and flow due dates.',
            github: 'https://github.com/moElhaj/aspnet-task-management',
            image: task
        }
    ];

    useEffect(() => {
        gsap.utils.toArray('.project-container').forEach(section => {
            gsap.to(section, 0, {
                opacity: 0,
                x: -90,
                scale: .9
            })
            gsap.to(section, .7, {
                scrollTrigger: {
                    trigger: section,
                    start: "top 80%",
                    end: 'bottom 25%'
                },
                opacity: 1,
                x: 0,
                scale: 1,
                stagger: .1
            });
        });
    }, [])
    return (
        <div className="work d-flex justify-content-center align-items-center">
            <section className="page-section portfolio" id="portfolio">
                <div className="container">

                    <h1 className="mb-5">Projects</h1>

                    <div className="row justify-content-center">

                        {projects.map((project, index) => (
                            <div key={index} className="project-container col-md-6 col-lg-4 mb-5">
                                <div className="project mx-auto">
                                    <div className="project-content d-flex flex-column h-100 w-100">
                                        <p className="project-title">{project.title}</p>
                                        <div className="d-flex project-tags">
                                            {project.tags.map((tag, index) => (
                                                <div className="tag flex v-center" key={index}><span className="tag-circle"></span>{tag}</div>
                                            ))}
                                        </div>
                                        <p className="project-details">
                                            {project.details}
                                        </p>
                                        <div className="d-flex project-footer">
                                            {project.github && <a className="d-flex v-center" href={project.github} target="_blank" rel="noopener noreferrer">
                                                <svg xmlns="http://www.w3.org/2000/svg" width="14" height="14" fill="currentColor" className="bi bi-github" viewBox="0 0 16 16">
                                                    <path d="M8 0C3.58 0 0 3.58 0 8c0 3.54 2.29 6.53 5.47 7.59.4.07.55-.17.55-.38 0-.19-.01-.82-.01-1.49-2.01.37-2.53-.49-2.69-.94-.09-.23-.48-.94-.82-1.13-.28-.15-.68-.52-.01-.53.63-.01 1.08.58 1.23.82.72 1.21 1.87.87 2.33.66.07-.52.28-.87.51-1.07-1.78-.2-3.64-.89-3.64-3.95 0-.87.31-1.59.82-2.15-.08-.2-.36-1.02.08-2.12 0 0 .67-.21 2.2.82.64-.18 1.32-.27 2-.27.68 0 1.36.09 2 .27 1.53-1.04 2.2-.82 2.2-.82.44 1.1.16 1.92.08 2.12.51.56.82 1.27.82 2.15 0 3.07-1.87 3.75-3.65 3.95.29.25.54.73.54 1.48 0 1.07-.01 1.93-.01 2.2 0 .21.15.46.55.38A8.012 8.012 0 0 0 16 8c0-4.42-3.58-8-8-8z" />
                                                </svg>
                                                Github
                                            </a>}
                                            {project.demo && <a className="d-flex v-center" href={project.demo} target="_blank" rel="noopener noreferrer">
                                                <svg xmlns="http://www.w3.org/2000/svg" width="14" height="14" fill="currentColor" className="bi bi-tv" viewBox="0 0 16 16">
                                                    <path d="M2.5 13.5A.5.5 0 0 1 3 13h10a.5.5 0 0 1 0 1H3a.5.5 0 0 1-.5-.5zM13.991 3l.024.001a1.46 1.46 0 0 1 .538.143.757.757 0 0 1 .302.254c.067.1.145.277.145.602v5.991l-.001.024a1.464 1.464 0 0 1-.143.538.758.758 0 0 1-.254.302c-.1.067-.277.145-.602.145H2.009l-.024-.001a1.464 1.464 0 0 1-.538-.143.758.758 0 0 1-.302-.254C1.078 10.502 1 10.325 1 10V4.009l.001-.024a1.46 1.46 0 0 1 .143-.538.758.758 0 0 1 .254-.302C1.498 3.078 1.675 3 2 3h11.991zM14 2H2C0 2 0 4 0 4v6c0 2 2 2 2 2h12c2 0 2-2 2-2V4c0-2-2-2-2-2z" />
                                                </svg>
                                                Demo
                                            </a>}
                                        </div>
                                    </div>
                                    <img className="img-fluid" src={project.image} alt="..." />
                                </div>
                            </div>
                        ))}

                    </div>
                </div>
            </section>
        </div>
    );
}

export default Work;