import React, { useEffect } from 'react';
import gsap from "gsap";
import { TextPlugin } from "gsap/TextPlugin";
import "../Styles/About.css";

gsap.registerPlugin(TextPlugin);

function About() {

    useEffect(() => {
        let starter = gsap.timeline();
        starter.to(['.logo','.contact-links'], .5, { y: 0 })
            .to(".line", { scaleX: 1, transformOrigin: "center center", delay: 1.5 }, "-=1")

        let tl = gsap.timeline({ delay: 1, repeat: -1 });
        tl.to('.upper-heading', .1, { text: "Mohamed Elhaj", y: "150px" })
            .to('.lower-text', .1, { text: "A Motivated Problem Solver who enjoys coding and building solution", y: "-150px" })

            // drop down
            .to('.upper-heading', 1, { y: 0 })
            .to('.lower-text', 1, { y: 0 }, "-=1")
            // hold
            .to('.lower-text', 7, { x: 0 })
            // hide
            .to('.upper-heading', 1, { y: "150px" })
            .to('.lower-text', 1, { y: "-150px" }, "-=1")

            ////////////////////////////////////////////////////// change text

            .to('.upper-heading', .1, { text: "Web Developer", y: "150px" })
            .to('.lower-text', .1, { text: "Experienced in Node.js, JavaScript, Python, React, React Native, GSAP, and more", y: "-150px" })

            // drop down
            .to('.upper-heading', 1, { y: 0 })
            .to('.lower-text', 1, { y: 0 }, "-=1")
            // hold
            .to('.lower-text', 7, { x: 0 })
            // hide
            .to('.upper-heading', 1, { y: "150px" })
            .to('.lower-text', 1, { y: "-150px" }, "-=1")

    }, [])

    function hoverAnimation() {
        let tl = gsap.timeline()
        tl.to('.dot', .3, { x: 10, ease: "back.out(1.7)" })
            .to('.dot', .3, { x: 0 })
    }

    return (
        <div id="about" className="about d-flex flex-column justify-content-center align-items-center position-relative">

            <div className="header w-100">
                <div className="container">
                    <div className="d-flex align-items-center w-100 p-20">
                        <div className="d-flex align-items-center logo" onClick={hoverAnimation}>mo<span className="dot">.</span></div>
                        <div className="spacer"></div>
                        <div className="contact-links d-flex justify-content-center align-items-center">
                            <a className="contact-link d-flex justify-content-center align-items-center" href="https://www.linkedin.com/in/mo-elhaj/" target="_blank" rel="noopener noreferrer">
                                <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 27 27"><path d="M4.98 3.5c0 1.381-1.11 2.5-2.48 2.5s-2.48-1.119-2.48-2.5c0-1.38 1.11-2.5 2.48-2.5s2.48 1.12 2.48 2.5zm.02 4.5h-5v16h5v-16zm7.982 0h-4.968v16h4.969v-8.399c0-4.67 6.029-5.052 6.029 0v8.399h4.988v-10.131c0-7.88-8.922-7.593-11.018-3.714v-2.155z" /></svg>
                            </a>
                            <a className="contact-link d-flex justify-content-center align-items-center" href="https://github.com/moelhaj" target="_blank" rel="noopener noreferrer">
                                <svg xmlns="http://www.w3.org/2000/svg" width="20" height="20" fill="currentColor" className="bi bi-github" viewBox="0 0 16 16">
                                    <path d="M8 0C3.58 0 0 3.58 0 8c0 3.54 2.29 6.53 5.47 7.59.4.07.55-.17.55-.38 0-.19-.01-.82-.01-1.49-2.01.37-2.53-.49-2.69-.94-.09-.23-.48-.94-.82-1.13-.28-.15-.68-.52-.01-.53.63-.01 1.08.58 1.23.82.72 1.21 1.87.87 2.33.66.07-.52.28-.87.51-1.07-1.78-.2-3.64-.89-3.64-3.95 0-.87.31-1.59.82-2.15-.08-.2-.36-1.02.08-2.12 0 0 .67-.21 2.2.82.64-.18 1.32-.27 2-.27.68 0 1.36.09 2 .27 1.53-1.04 2.2-.82 2.2-.82.44 1.1.16 1.92.08 2.12.51.56.82 1.27.82 2.15 0 3.07-1.87 3.75-3.65 3.95.29.25.54.73.54 1.48 0 1.07-.01 1.93-.01 2.2 0 .21.15.46.55.38A8.012 8.012 0 0 0 16 8c0-4.42-3.58-8-8-8z" />
                                </svg>
                            </a>
                        </div>
                    </div>
                </div>
            </div>

            <div className="container">

                <div className="about-content d-flex flex-column justify-content-center align-items-center p-20">

                    <div className="upper flex center">
                        <div className="overflow-hidden position-relative">
                            <h1 className="upper-heading">Mohamed Elhaj</h1>
                        </div>
                    </div>

                    <div className="line"></div>

                    <div className="lower flex center">
                        <div className="overflow-hidden position-relative">
                            <p className="lower-text">A Motivated Problem Solver who enjoys coding and building solution</p>
                        </div>
                    </div>

                </div>

            </div>

        </div>
    );
}

export default About;
